"use client";

import { useState } from "react";

export default function QuickConsultForm() {
  const [isOpen, setIsOpen] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    phone: "",
    area: "",
    treatment: "",
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // 폼 제출 로직
    console.log("Form submitted:", formData);
    alert("상담 신청이 완료되었습니다.");
    setIsOpen(false);
  };

  return (
    <>
      {/* 플로팅 버튼 */}
      <button
        onClick={() => setIsOpen(true)}
        className="fixed bottom-8 right-8 z-50 bg-[#1a1a1a] text-white px-6 py-4 rounded-full shadow-lg hover:bg-[#2a2a2a] transition-all duration-300 flex items-center gap-2"
      >
        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
        </svg>
        <span className="hidden md:inline">빠른 상담</span>
      </button>

      {/* 모달 오버레이 */}
      {isOpen && (
        <div
          className="fixed inset-0 z-[100] bg-black/50 flex items-center justify-center p-4"
          onClick={() => setIsOpen(false)}
        >
          <div
            className="bg-white rounded-lg max-w-md w-full p-8 relative"
            onClick={(e) => e.stopPropagation()}
          >
            <button
              onClick={() => setIsOpen(false)}
              className="absolute top-4 right-4 text-[#666] hover:text-[#1a1a1a] transition-colors"
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
            <h3 className="text-2xl font-light mb-6 text-[#1a1a1a]">빠른 상담신청</h3>
            <form onSubmit={handleSubmit} className="space-y-4">
              <input
                type="text"
                placeholder="이름"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                className="w-full px-4 py-3 border border-[#e8e8e8] rounded-sm focus:outline-none focus:border-[#1a1a1a] transition-colors"
                required
              />
              <input
                type="tel"
                placeholder="연락처"
                value={formData.phone}
                onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                className="w-full px-4 py-3 border border-[#e8e8e8] rounded-sm focus:outline-none focus:border-[#1a1a1a] transition-colors"
                required
              />
              <input
                type="text"
                placeholder="상담 부위"
                value={formData.area}
                onChange={(e) => setFormData({ ...formData, area: e.target.value })}
                className="w-full px-4 py-3 border border-[#e8e8e8] rounded-sm focus:outline-none focus:border-[#1a1a1a] transition-colors"
              />
              <select
                value={formData.treatment}
                onChange={(e) => setFormData({ ...formData, treatment: e.target.value })}
                className="w-full px-4 py-3 border border-[#e8e8e8] rounded-sm focus:outline-none focus:border-[#1a1a1a] transition-colors"
              >
                <option value="">상담시술을 선택해 주세요.</option>
                <option value="uline-cut">유라인컷</option>
                <option value="face-cut">페이스컷</option>
                <option value="body-cut">바디컷</option>
                <option value="titanium-lifting">티타늄 리프팅</option>
              </select>
              <div className="flex items-center gap-2 text-sm text-[#666]">
                <input type="checkbox" id="privacy" required className="w-4 h-4" />
                <label htmlFor="privacy">개인정보 수집/이용 동의하기</label>
              </div>
              <button
                type="submit"
                className="w-full bg-[#1a1a1a] text-white px-6 py-4 rounded-sm font-medium hover:bg-[#2a2a2a] transition-colors duration-300"
              >
                상담신청
              </button>
            </form>
          </div>
        </div>
      )}
    </>
  );
}
